@import "../../inc/vars";
.acro-slider-with-text {
	&__container {
		padding: 24px 0;
		display: flex;
		flex-wrap: wrap;
		@media (min-width: $screen-sm) {
			padding: 40px 55px;
		}
	}
	&__slides {
		width: 100%;
		display: flex;
		justify-content: center;
		padding: 0 20px;
		@media (min-width: $screen-md) {
			width: 60%;
			padding: 0;
		}
		.macbook {
			position: relative;
			overflow: hidden;
			margin-bottom: 40px;
			filter: drop-shadow(2px 4px 20px rgba(0,0,0,0.15));
			img {
				max-width: 100%;
				height: auto;
				width: 100% !important;
			}
			.slides {
				position: absolute;
				right: 4.1%;
				width: auto;
				top: 5.5%;
				left: 4.1%;
				.slick-slide {
					opacity: 0;
					transition: .5s;
					&.slick-current {
						opacity: 1;
					}
				}
			}
		}
	}
	&__text {
		width: 100%;
		padding-top: 40px;
		padding-left: 30px;
		padding-right: 30px;
		@media (min-width: $screen-md) {
			width: 40%;
			padding-left: 15px;
			padding-right: 15px;
		}
		.subtitle {
			@include pop(400, 18, 1.24);
			color: #5A6272;
			margin-bottom: 16px;
			@media(min-width: $screen-sm) {
				@include pop(400, 24, 1.24);
			}
		}
		h2 {
			@include pop(400, 35, 1.24);
			background: var(--grad-title, linear-gradient(180deg, #2D3442 0%, #6C7689 100%));
			background-clip: text;
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
			margin-bottom: 16px;
		}
		.description {
			@include fig(400, 18, 1.36);
			color: #4F4B58;
		}
	}
}